.elements {
  display: grid;
  grid-template-columns: repeat(auto-fill, 282px);
  row-gap: 20px;
  column-gap: 17px;
  margin: 50px auto 0;
  max-width: 880px;
  flex-grow: 1;
}

@media screen and (max-width: 1023px) {
  .elements {
    justify-content: center;
    margin-top: 36px;
  }
}
